.about {
    background-color: #f9f9f9; /* Light background color */
    padding: 60px 20px; /* Padding around the section */
    text-align: center; /* Center-align text */
  }
  
  .about .container {
    max-width: 900px; /* Max width for the content */
    margin: 0 auto; /* Center the container */
    padding: 20px; /* Padding inside the container */
    background: #fff; /* White background for the container */
    border-radius: 8px; /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  }
  
  .about h2 {
    font-size: 2.5em; /* Larger font size for the heading */
    margin-bottom: 20px; /* Margin below the heading */
    color: #333; /* Dark color for the heading */
  }
  
  .about p {
    font-size: 1.2em; /* Larger font size for paragraphs */
    line-height: 1.6; /* Increase line height for readability */
    color: #555; /* Medium-dark color for text */
    margin-bottom: 20px; /* Margin below paragraphs */
    text-align: left; /* Align text to the left */
  }
  
  .about ul {
    list-style-type: none; /* Remove default list styling */
    padding: 0; /* Remove padding */
  }
  
  .about li {
    font-size: 1.1em; /* Font size for list items */
    margin-bottom: 10px; /* Margin below list items */
    text-align: left; /* Align list items to the left */
  }
  
  .about strong {
    color: #333; /* Dark color for strong text */
  }
  
  @media (min-width: 768px) {
    .about p {
      font-size: 1.3em; /* Larger font size on larger screens */
    }
  }
  